import React from 'react';
import PropTypes from 'prop-types';


import classNames from 'classnames';

import { theme } from './Button';

import {
	handleWaveMouseDown,
	handleWaveMouseUp,
	handleWaveMouseLeave,
} from './Wave';

import './button.css';
import './effects.css';

import './button-waves.css';
import './button-waves-effects.css'

const thisTheme = {
	...theme,
	CLASSES: 'rmd-btn rmd-btn__waves',
	CLASSES_WAVE: 'rmd-btn--wave',

	CLASS_ACTIVE: 'rmd-btn__waves-active',
	CLASS_ACTIVE_END: 'rmd-btn__waves-active-end',
	CLASS_ACTIVE_RESET: 'rmd-btn__waves-active-reset',

	ANIMATION_TIME_MIN: 400,
};

export { thisTheme as theme };


const handleMouseDown = (refs, props, theme, e) => {

	handleWaveMouseDown(refs, props, theme, e);

	if (refs.$wave) {
		const { $wave } = refs;

		const top = e.offsetY || e.nativeEvent.offsetY;
		const left = e.offsetX || e.nativeEvent.offsetX;
		$wave.style.cssText = `top: ${top}px; left: ${left}px;`;
	}

	if (props.onMouseDown) {
		return props.onMouseDown(e);
	}
};


const handleMouseUp = (refs, props, theme, e) => {
	handleWaveMouseUp(refs, props, theme, e);

	if (props.onMouseUp) {
		return props.onMouseUp(e);
	}
};

const handleMouseLeave = (refs, props, theme, e) => {
	handleWaveMouseLeave(refs, props, theme, e);

	if (props.onMouseLeave) {
		return props.onMouseLeave(e);
	}
};

const ButtonWaves = ({ _theme = thisTheme, type, color, ...props }) => {

	const refs = { $button: undefined, $wave: undefined };

	return (
		<button
			{ ...props }
			className={ classNames(_theme.CLASSES, props.class, props.className, {
				[ _theme.CLASSES_FLAT ]: type === 'flat' || type === undefined,
				[ _theme.CLASSES_RAISED ]: type === 'raised',
				[ _theme.CLASSES_FAB ]: type === 'fab',
				[ _theme.CLASSES_DEFAULT ]: color === 'default' || color === undefined,
				[ _theme.CLASSES_PRIMARY ]: color === 'primary',
				[ _theme.CLASSES_SECONDARY ]: color === 'secondary',
			}) }
			ref={ el => refs.$button = el }
			onMouseDown={ handleMouseDown.bind(undefined, refs, props, _theme) }
			onMouseUp={ handleMouseUp.bind(undefined, refs, props, _theme) }
			onMouseLeave={ handleMouseLeave.bind(undefined, refs, props, _theme) }
		>
			<div className={ _theme.CLASSES_WAVE } ref={ el => refs.$wave = el }></div>
			{ props.children }
		</button>
	);
};

ButtonWaves.propTypes = {
	type: PropTypes.oneOf(['flat', 'raised', 'fab']),
	color: PropTypes.oneOf(['default', 'primary', 'secondary']),
};


export default ButtonWaves;



