import React, { Component } from 'react';

import ButtonSection from './Buttons';
import TooltipSection from './Tooltips';
import InputsSection from './Inputs';


import './App.css';

class App extends Component {
    render() {
        return (
            <div className="cv-demo">
                <header className="cv-header">
                    <h1>React Components</h1>
                </header>
                <div className="cv-container">
                    <ButtonSection/>
                    <TooltipSection/>
                    <InputsSection/>
                </div>
            </div>
        );
    }
}

export default App;



