import PopUp from '../Menu/PopUp';

class Menu extends PopUp {

	static CLASS_OPEN = 'rmd-menu-popup__open';
	static SET_UPDATED_MENU_POSITION_ANIMATION_DELAY = 300;

	componentDidMount() {

		this.setMenuPosition(() => {
			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					if (this.$popUp) {
						this.$popUp.classList.add(this.constructor.CLASS_OPEN);
					}
				});
			});
		});

		if (super.componentDidMount) {
			return super.componentDidMount.apply(this, arguments);
		}
	}


	componentDidUpdate(nextProps, nextState) {

		setTimeout(() =>
			this.setMenuPosition()
		, this.constructor.SET_UPDATED_MENU_POSITION_ANIMATION_DELAY);


		if (super.componentDidUpdate) {
			return super.componentDidUpdate.apply(this, arguments);
		}
	}

	setMenuPosition(callback) {
		if (this.$popUp) {
			const $selected = this.$popUp.querySelector('[data-selected]');

			if (! $selected) {
				if (callback) {
					callback();
				}

				return;
			}

			const popUpHeight = this.$popUp.clientHeight;
			const popUpScrollHeight = this.$popUp.scrollHeight;
			const selectedOffsetTop = $selected.offsetTop;
			let offsetTop = selectedOffsetTop;

			if (popUpHeight < popUpScrollHeight) {
				this.$popUp.scrollTop = selectedOffsetTop - (popUpHeight / 2);

				if (popUpHeight / 2 < selectedOffsetTop) {
					offsetTop = popUpHeight / 2;
				}

				const bottomDiff =  (popUpScrollHeight - selectedOffsetTop) - (popUpHeight / 2);

				if (bottomDiff < 0) {
					offsetTop -= bottomDiff;
				}
			}


			const newState = {
				top: -offsetTop + 'px',
				transformOrigin: 'left ' + (offsetTop + ($selected.offsetHeight / 2)) + 'px 0',
			};

			if (this.state.top !== newState.top || this.state.transformOrigin !== newState.transformOrigin) {
				this.setState(newState, () => {
					if (callback) {
						callback();
					}
				});
			}
		}

	}
}

export default Menu;



