import React, { Component } from 'react';

import PropTypes from 'prop-types';


import slot from '../../Common/Util/slot';

import {
	handleWaveMouseDown,
	handleWaveMouseUp,
	handleWaveMouseLeave,
} from '../Button/Wave';


import classNames from 'classnames';

import './select.css';
import './select-effects.css';

require('../../Common/Polyfill/element-closest');

export const theme = {
	CLASS_MENU_ITEM: 'rmd-menu-popup--item',
	CLASS_WRAPPER: 'rmd-select--wrapper',
	CLASS_MENU_OPEN: 'rmd-menu-popup__open',

	CLASSES: 'rmd-select',
	CLASSES_WRAPPER: 'rmd-select--wrapper rmd-menu-popup--container',
	CLASSES_CONTENT: 'rmd-select--content',
	CLASSES_ICON_WRAPPER: 'rmd-select--icon-wrapper',
	CLASSES_ICON_DOWN: 'rmd-select--icon-down',

	CLASSES_WAVE: 'rmd-select--wave',
	CLASSES_ICON_CONTENT: 'rmd-select--icon-content',

	CLASS_ACTIVE: 'rmd-select__active',
	CLASS_ACTIVE_END: 'rmd-select__active-end',
	CLASS_ACTIVE_RESET: 'rmd-select__active-reset',

	ANIMATION_TIME_MIN: 400,

	MENU_HIDE_ANIMATION_TIME: 450 + 200,
};


class Select extends Component {
	constructor(props) {
		super(props);

		this.state = {
			menuShow: props.openImmediately,
		};

		this.realMenuShow = this.state.menuShow;

		this.theme = theme;

		this._refs = {
			$button: undefined,
			$wrapper: undefined,
		};

		this.handleButtonRef = this.handleButtonRef.bind(this);
		this.handleWrapperRef = this.handleWrapperRef.bind(this);

		this.handleMouseDown = this.handleMouseDown.bind(this);
		this.handleMouseUp = this.handleMouseUp.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
		this.handleFocus = this.handleFocus.bind(this);
		this.handleBlur = this.handleBlur.bind(this);

		this.handleClick = this.handleClick.bind(this);
		this.handleWrapperClick = this.handleWrapperClick.bind(this);
		this.handleDocumentMouseDown = this.handleDocumentMouseDown.bind(this);
	}

	componentDidMount() {
		if (this.props.openImmediately) {
			this.setListeners();
		}
	}

	componentWillUnmount() {
		this.setListeners(false);
	}

	componentWillUpdate(nextProps, nextState) {
		if (this.state.menuShow !== nextState.menuShow) {
			if (nextState.menuShow) {
				this.setListeners();
			} else {
				this.setListeners(false);
			}
		}
	}

	setListeners(add = true) {
		if (add) {
			document.addEventListener('mousedown', this.handleDocumentMouseDown);
		} else {
			document.removeEventListener('mousedown', this.handleDocumentMouseDown);
		}
	}

	handleMouseDown(e) {

		const { _refs: refs, props, theme } = this;

		handleWaveMouseDown(refs, props, theme, e);

		if (refs.$button) {
			const { $button } = refs;

			// for focus wave effect
			$button.dataset.mouseFocus = '1';
			// FF on Mac, Safari bug
			$button.focus();
		}

		if (props.onMouseDown) {
			return props.onMouseDown(e);
		}
	};


	handleMouseUp(e) {

		const { _refs: refs, props, theme } = this;

		handleWaveMouseUp(refs, props, theme, e);

		if (props.onMouseUp) {
			return props.onMouseUp(e);
		}
	};

	handleMouseLeave(e) {

		const { _refs: refs, props, theme } = this;

		handleWaveMouseLeave(refs, props, theme, e);

		if (props.onMouseLeave) {
			return props.onMouseLeave(e);
		}
	};

	handleFocus(e) {

		// eslint-disable-next-line
		const { _refs: refs, props, theme } = this;

		if (props.onFocus) {
			return props.onFocus(e);
		}
	};

	handleBlur(e) {

		// eslint-disable-next-line
		const { _refs: refs, props, theme } = this;

		if (refs.$button) {
			const { $button } = refs;

			delete $button.dataset.mouseFocus;
		}

		if (props.onBlur) {
			return props.onBlur(e);
		}
	};


	showMenu() {
		this.realMenuShow = true;

		this.setState({ menuShow: true });

		const { _refs: refs, theme } = this;

		if (refs.$wrapper) {
			const { $wrapper } = refs;

			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					$wrapper.classList.add(theme.CLASS_MENU_OPEN);
				});
			});
		}
	}


	hideMenu() {
		this.realMenuShow = false;

		setTimeout(() => {
			if ( this.realMenuShow || ! this.state.menuShow) {
				return;
			}

			this.setState({ menuShow: false });
		}, this.theme.MENU_HIDE_ANIMATION_TIME);


		const { _refs: refs, theme } = this;

		if (refs.$wrapper) {
			const { $wrapper } = refs;

			requestAnimationFrame(() => {
				requestAnimationFrame(() => {
					$wrapper.classList.remove(theme.CLASS_MENU_OPEN);
				});
			});
		}
	}


	handleClick(e) {
		if (this.realMenuShow) {
			this.hideMenu();
		} else {
			this.showMenu();
		}

	}

	handleDocumentMouseDown(e) {
		if (e.target.closest('.' + this.theme.CLASS_WRAPPER) !== this._refs.$wrapper) {
			this.hideMenu();
		}
	}

	handleWrapperClick(e) {
		if (! this.state.menuShow) {
			return;
		}

		if (e.target.closest('.' + this.theme.CLASS_WRAPPER) === this._refs.$wrapper &&
			e.target.closest('.' + this.theme.CLASS_MENU_ITEM)) {
			this.hideMenu();
		}
	}

	handleButtonRef(el) {
		this._refs.$button = el;
	}

	handleWrapperRef(el) {
		this._refs.$wrapper = el;
	}

	render() {

		const { style, ...props  } = this.props;

		const _theme = this.theme;

		const { main, menu } = slot(this.props.children);

		return (
			<div
				style={ style || props.wrapperStyle }
				className={ classNames(_theme.CLASSES_WRAPPER, props.wrapperClass) }
				ref={ this.handleWrapperRef }
				onClick={ this.handleWrapperClick }
			>
				<button
					{ ...props }
					style={ style || props.btnStyle }
					className={ classNames(_theme.CLASSES, props.class, props.className, props.btnClass, {
					}) }
					ref={ this.handleButtonRef }
					onMouseDown={ this.handleMouseDown }
					onMouseUp={ this.handleMouseUp }
					onMouseLeave={ this.handleMouseLeave }
					onFocus={ this.handleFocus }
					onBlur={ this.handleBlur }

					onClick={ this.handleClick }
				>
					<div className={ _theme.CLASSES_CONTENT }>
						{ main }
					</div>
					<div className={ _theme.CLASSES_ICON_WRAPPER }>
						<div className={ _theme.CLASSES_WAVE }></div>
						<div className={ _theme.CLASSES_ICON_CONTENT }>
							<svg viewBox="0 0 24 24" className={ _theme.CLASSES_ICON_DOWN }><path d="M7 10l5 5 5-5z"></path></svg>
						</div>
					</div>
				</button>
				{ this.state.menuShow ? menu : null }
			</div>
		);
	}
}

Select.propTypes = {
	openImmediately: PropTypes.bool,
	btnStyle: PropTypes.any,
 	wrapperStyle: PropTypes.any,
 	btnClass: PropTypes.string,
 	wrapperClass: PropTypes.string,
};


export default Select;



