import React from 'react';

import Button from './lib/MaterialDesignTheme/Button/Button';
import ButtonWaves from './lib/MaterialDesignTheme/Button/ButtonWaves';
import IconButton from './lib/MaterialDesignTheme/Button/IconButton';


import mailBlack from './img/ic_mail_black_24px.svg';
import sendWhite from './img/ic_send_white_24px.svg';
import shareBlack from './img/ic_share_black_24px.svg';
import shareWhite from './img/ic_share_white_24px.svg';

const btnStyle = { margin: '1rem' };

const Buttons = () => {
	return (
		<section className="cv-card">
			<h1>Buttons</h1>

			<section>
				<h2>Flat Buttons</h2>

				<div className="cv-buttons">
					<Button
						style={ btnStyle }
					>
						Default
					</Button>
					<Button
						color="primary"
						style={ btnStyle }
					>
						Primary
					</Button>
					<Button
						color="secondary"
						style={ btnStyle }
					>
						Secondary
					</Button>
					<Button
						style={ btnStyle }
						disabled
					>
						Disabled
					</Button>
				</div>

			</section>
			<section>
				<h2>Flat Buttons with wave effect</h2>

				<div className="cv-buttons">
					<ButtonWaves
						style={ btnStyle }
					>
						Default
					</ButtonWaves>
					<ButtonWaves
						color="primary"
						style={ btnStyle }
					>
						Primary
					</ButtonWaves>
					<ButtonWaves
						color="secondary"
						style={ btnStyle }
					>
						Secondary
					</ButtonWaves>
					<ButtonWaves
						style={ btnStyle }
						disabled
					>
						Disabled
					</ButtonWaves>
				</div>

				<ButtonWaves
					style={{ width: '100%' }}
				>
					Full width button
				</ButtonWaves>

			</section>

			<section>
				<h2>Raised Button</h2>

				<div className="cv-buttons">
					<Button
						type="raised"
						style={ btnStyle }
					>
						Default
					</Button>
					<Button
						type="raised"
						color="primary"
						style={ btnStyle }
					>
						Primary
					</Button>
					<Button
						type="raised"
						color="secondary"
						style={ btnStyle }
					>
						Secondary
					</Button>
					<Button
						type="raised"
						style={ btnStyle }
						disabled
					>
						Disabled
					</Button>
				</div>

			</section>

			<section>
				<h2>Raised Button with wave effect</h2>

				<div className="cv-buttons">
					<ButtonWaves
						type="raised"
						style={ btnStyle }
					>
						Default
					</ButtonWaves>
					<ButtonWaves
						type="raised"
						color="primary"
						style={ btnStyle }
					>
						Primary
					</ButtonWaves>
					<ButtonWaves
						type="raised"
						color="secondary"
						style={ btnStyle }
					>
						Secondary
					</ButtonWaves>
					<ButtonWaves
						type="raised"
						style={ btnStyle }
						disabled
					>
						Disabled
					</ButtonWaves>
				</div>
			</section>

			<section>
				<h2>Floating Action Button</h2>

				<div className="cv-buttons">
					<ButtonWaves
						style={ btnStyle }
						type="fab"
					>
						<img style={{ opacity: '0.6' }} src={ mailBlack } alt="Mail"/>
					</ButtonWaves>
					<ButtonWaves
						color="primary"
						type="fab"
						style={ btnStyle }
					>
						<img style={{ marginLeft: '-4%' }} src={ shareWhite } alt="Share"/>
					</ButtonWaves>
					<ButtonWaves
						color="secondary"
						type="fab"
						style={ btnStyle }
					>
						<img style={{ marginLeft: '8%' }} src={ sendWhite } alt="Send"/>
					</ButtonWaves>
					<ButtonWaves
						type="fab"
						disabled
					>
						<img style={{ opacity: '0.6', marginLeft: '-4%' }} src={ shareBlack } alt="Share"/>
					</ButtonWaves>
				</div>
			</section>

			<section>
				<h2>Icon Button</h2>

				<div className="cv-buttons">
					<IconButton
						style={ btnStyle }
					>
						<img style={{ marginLeft: '-4%' }} src={ shareBlack } alt="Share"/>
					</IconButton>

					<IconButton
						disabled
						style={ btnStyle }
					>
						<img style={{ opacity: '0.6', marginLeft: '-4%' }} src={ shareBlack } alt="Share"/>
					</IconButton>

					<div style={{ background: 'rgba(0, 0, 0, .5)' }}>
						<IconButton
							inverse={ true }
							style={ btnStyle }
						>
							<img style={{ marginLeft: '8%' }} src={ sendWhite } alt="Share"/>
						</IconButton>

						<IconButton
							inverse={ true }
							disabled
							style={ btnStyle }
						>
							<img style={{ opacity: '0.6', marginLeft: '8%' }} src={ sendWhite } alt="Share"/>
						</IconButton>
					</div>
				</div>
			</section>
		</section>
	);
};

export default Buttons;


